// src/pages/admin/layout.js
import Link from "next/link";
import { useTheme } from "next-themes";
import { Sun, Moon, ShieldCheck } from "lucide-react";
import { useEffect, useState } from "react";

export default function AdminLayout({ children }) {
  const { theme, setTheme } = useTheme();
  const [mounted, setMounted] = useState(false);

  useEffect(() => setMounted(true), []);
  if (!mounted) return null;

  return (
    <div className="min-h-screen flex bg-gray-50 dark:bg-gray-900 transition-colors duration-300">
      {/* Sidebar */}
      <aside className="w-64 bg-white dark:bg-gray-800 border-r border-gray-200 dark:border-gray-700 p-5 flex flex-col justify-between">
        <div>
          <div className="flex items-center mb-6">
            <ShieldCheck size={24} className="text-orange-500 mr-2" />
            <h1 className="text-lg font-bold text-gray-800 dark:text-gray-100">
              Khedme Admin
            </h1>
          </div>
          <nav className="space-y-2">
            <Link
              href="/admin"
              className="block px-3 py-2 rounded-lg text-gray-700 dark:text-gray-300 hover:bg-orange-100 dark:hover:bg-orange-500/20 transition"
            >
              Dashboard
            </Link>
            <Link
              href="/admin/approve"
              className="block px-3 py-2 rounded-lg text-gray-700 dark:text-gray-300 hover:bg-orange-100 dark:hover:bg-orange-500/20 transition"
            >
              Provider Approvals
            </Link>
            <Link
              href="/admin/jobs"
              className="block px-3 py-2 rounded-lg text-gray-700 dark:text-gray-300 hover:bg-orange-100 dark:hover:bg-orange-500/20 transition"
            >
              Manage Jobs
            </Link>
          </nav>
        </div>

        {/* Theme Toggle */}
        <button
          onClick={() => setTheme(theme === "dark" ? "light" : "dark")}
          className="flex items-center justify-center gap-2 px-3 py-2 rounded-lg bg-gray-100 dark:bg-gray-700 text-gray-700 dark:text-gray-300 hover:bg-orange-100 dark:hover:bg-orange-500/20 transition"
        >
          {theme === "dark" ? <Sun size={16} /> : <Moon size={16} />}
          {theme === "dark" ? "Light Mode" : "Dark Mode"}
        </button>
      </aside>

      {/* Main Content */}
      <main className="flex-1 p-6 overflow-y-auto">{children}</main>
    </div>
  );
}
